.page-footer__icon {
  animation-name: heartBeat;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes heartBeat {
  0% {
    transform: scale(1);
  }

  20% {
    transform: scale(1.2);
  }

  35% {
    transform: scale(.8);
  }

  50% {
    transform: scale(1.2);
  }

  65% {
    transform: scale(.8);
  }

  80% {
    transform: scale(1);
  }

  100% {
    transform: scale(1);
  }
}

.page-footer__link {
  color: inherit;
  text-decoration: underline;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

