.header-library{
        padding-top: 40px;
        padding-bottom: 60px;
        background-color: rgba(0, 0, 0, 0.56);
        background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)),
          url('../images/header-watched-320/Rectangle-1-min.jpg');
        background-size: cover;
    }

.header-library-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 66px;
}

.header-library-logo__text{
    display: none;
}

.header-library-nav__list {
    display: flex;

}

.header-library-nav__item:not(:last-child){
    margin-right: 40px;
}

.header-library__link {
    position: relative;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 1.2;
    color: var(--white);
}

.header-library__link__current::after{
    position: absolute;
    content: '';
    left: 0;
    bottom: 0;
    margin-bottom: -3px;
    width: 100%;
    height: 4px;
    background-color: var(--header-line);
}

.header-library-box-button{
    display: flex;
    justify-content: center;
}

.header-library__button {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.3;
    color: var(--white);
    padding: 14px 36px 12px 36px;
    cursor: pointer;

    background-color: transparent;
    border: 1px solid #FFFFFF;
    border-radius: 5px;

    &:not(:last-child){
        margin-right: 20px;
    }

    &:hover,
    &:focus {
        background-color: var(--ogange-two);
        border: 1px solid var(--ogange-two);
        box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
    }
}

@media screen and (min-device-pixel-ratio: 2) and (max-width: 767px),
 screen and (min-resolution: 192dpi) and (max-width: 767px),
 screen and (min-resolution: 2dppx) and (max-width: 767px) {
    .header-library{
        background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)),
        url('../images/header-watched-320/Rectangle-1@2x-min.jpg');
    }
 }

