//---------ADAPTIVE(768PX)------------

@media screen and (min-width: 768px){
    .header-library{
        background-color: rgba(0, 0, 0, 0.56);
        background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)),
          url('../images/header-watched-768/Rectangle-1-min.jpg');
        background-size: cover;
    }


    .header-library-logo__text{
        display: inline;
        font-family: var(--font-family);
        color: var(--white);
        font-style: normal;
        font-weight: 500;
        font-size: 30px;
        line-height: 1.2;
        margin-left: 12px;
    }
}


@media screen and (min-device-pixel-ratio: 2) and (min-width: 768px),
screen and (min-resolution: 192dpi) and (min-width: 768px),
screen and (min-resolution: 2dppx) and (min-width: 768px) {
    .header-library{
       background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)),
       url('../images/header-watched-768/Rectangle-1@2x-min.jpg');
       }
    }
//------- adaptive(1200px)-------


@media screen and (min-width: 1280px) {
    .header-library{
        background-color: rgba(0, 0, 0, 0.56);
        background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)),
          url('../images/header-watched-1280/Rectangle-1-min.jpg');
        background-size: cover;
    }


    .header-library-nav {
        margin-bottom: 40px;
    }

    
    .header-library-nav__item:not(:last-child){
            margin-right: 40px;
    }

    @media screen and (min-device-pixel-ratio: 2) and (min-width: 1280px),
    screen and (min-resolution: 192dpi) and (min-width: 1280px),
    screen and (min-resolution: 2dppx) and (min-width: 1280px) {
        .header-library{
           background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)),
           url('../images/header-watched-1280/Rectangle-1@2x-min.jpg');
           }
        }

